import { OrbitControls, PerspectiveCamera, Sky } from "@react-three/drei";
import React, { FC, Fragment, Suspense } from "react";
import { Canvas } from "react-three-fiber";
import GlobalStyles from "../style/GlobalStyles";
import LeafBlower from "./Models/120iB";

const App: FC = () => {
  return (
    <Fragment>
      <GlobalStyles />
      <Canvas gl={{ antialias: true }} pixelRatio={window.devicePixelRatio}>
        <PerspectiveCamera>
          <OrbitControls
            target={[0, 0, 4.1]}
            enablePan={false}
            autoRotate
            maxDistance={1}
            minDistance={0.6}
          />
          <Sky
            distance={1000000} // Camera distance (default=450000)
            sunPosition={[0, 1, 0]} // Sun position normal (defaults to inclination and azimuth if not set)
            inclination={0.5} // Sun elevation angle from 0 to 1 (default=0)
            azimuth={0.25}
          />
          <ambientLight />
          <pointLight intensity={1} position={[0.5, 0, 7]} />
          <pointLight intensity={0.5} position={[-0.84, 10, 1]} />
          <Suspense fallback={null}>
            <LeafBlower />
          </Suspense>
        </PerspectiveCamera>
      </Canvas>
    </Fragment>
  );
};

export default App;
